 body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

ul {
	list-style-type: none;
	margin: 0;
	padding: 0;
	height:100px;
	background-color: white;
	text-align: right;

	/*border-bottom: 1px solid #1850A5;*/
}
ul li {
	display: -webkit-inline-flex;
	display: inline-flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-justify-content: center;
	        justify-content: center;
	margin-left: 40px;
	margin-right: 20px;
	margin-top: 35px;
	font-size: 32px;
}
ul li.ft {
	float: left;
}
ul li:hover {
	opacity: .5;
}


.bottomPart {
  /*border-top: 1px black solid;*/
  /*border-top-width: 75%;*/
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 0;
}
.svg-inline--fa {
  vertical-align: -0.200em;
}

.rounded-social-buttons {
  text-align: center;
  margin-bottom: 40px;
  margin-top: 40px;
}

.rounded-social-buttons .social-button {
  display: inline-block;
  position: relative;
  cursor: pointer;
  width: 3.125rem;
  height: 3.125rem;
  border: 0.125rem solid transparent;
  padding: 0;
  text-decoration: none;
  text-align: center;
  color: #fefefe;
  font-size: 1.5625rem;
  font-weight: normal;
  line-height: 2em;
  border-radius: 1.6875rem;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  margin-right: 0.25rem;
  margin-bottom: 0.25rem;
}

.rounded-social-buttons .social-button:hover, .rounded-social-buttons .social-button:focus {
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
}

.rounded-social-buttons .fa-twitter, .fa-facebook-f, .fa-linkedin, .fa-youtube, .fa-instagram {
  font-size: 25px;
}

.rounded-social-buttons .social-button.facebook {
  background: #3b5998;
}

.rounded-social-buttons .social-button.facebook:hover, .rounded-social-buttons .social-button.facebook:focus {
  color: #3b5998;
  background: #fefefe;
  border-color: #3b5998;
}

.rounded-social-buttons .social-button.twitter {
  background: #55acee;
}

.rounded-social-buttons .social-button.twitter:hover, .rounded-social-buttons .social-button.twitter:focus {
  color: #55acee;
  background: #fefefe;
  border-color: #55acee;
}

.rounded-social-buttons .social-button.linkedin {
  background: #007bb5;
}

.rounded-social-buttons .social-button.linkedin:hover, .rounded-social-buttons .social-button.linkedin:focus {
  color: #007bb5;
  background: #fefefe;
  border-color: #007bb5;
}

.rounded-social-buttons .social-button.youtube {
  background: #bb0000;
}

.rounded-social-buttons .social-button.youtube:hover, .rounded-social-buttons .social-button.youtube:focus {
  color: #bb0000;
  background: #fefefe;
  border-color: #bb0000;
}

.rounded-social-buttons .social-button.instagram {
  background: #125688;
}

.rounded-social-buttons .social-button.instagram:hover, .rounded-social-buttons .social-button.instagram:focus {
  color: #125688;
  background: #fefefe;
  border-color: #125688;
}



body{
  background-repeat:none;
}

.container{
  display: -webkit-inline-flex;
  display: inline-flex;
  margin:auto;
  width:33%;

}

.article-card{
  width:300px;
  color:#000;
  border-radius:15px;
  background: #fff;
  margin:auto;
  box-shadow:-1px 15px 30px -12px black;
}

.article-card .author{
  padding:10px;
  min-height:60px;
}

.article-card .author .user-avatar {
  float:left;
  margin-right:15px;
  display:inline-block;
}

.article-card .author .user-avatar img{
  max-height:50px;
  border-radius:50%;
}

.article-card .author .user-name{
  font-size:15px;
  font-weight:bold;
}

.article-card .author .written-date{
  color:#777;
  font-size:12px;
}

.article-card .content-image img{
  width:100%;
}

.article-card .content-text{
  padding-left:10px;
  padding-right:10px;
  margin:20px 0 20px 0;
}

.article-card .footer{
  color:#fff;
  background-color:#e85858;
  border-bottom-left-radius:15px;
  border-bottom-right-radius:15px;
}

.article-card .footer .part{
  width:28%;
  padding:5px;
  padding-top:20px;
  padding-bottom:20px;
  text-align:center;
  display:inline-block;
  border-right:1px solid #757575;
}

.article-card .footer .part:last-child{
  border:none;
}

.article-card .footer .part i{
  font-size:20px;
}

.article-card .footer .part .count-text{
  display:block;
  font-size: 24px;
  font-weight:bold;
}
